import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import "./index.css";
import words4 from "../data/words-6.json";

const pick = arr => {
  const ndx = Math.floor(Math.random() * arr.length);
  return arr[ndx];
};

const reduce = (state, action = {}) => {
  const { type } = action;
  switch (type) {
    case "INIT":
      const target = pick(words4).toUpperCase().split("");
      const current = [];
      return { target, current };
    case "ADD_LETTER":
      state.current.push(action.letter);
      return state;
    case "DELETE_LETTER":
      state.current.pop();
      return state;
    default:
      return state;
  }
};

const initialState = reduce({}, { type: "INIT" });

ReactDOM.render(
  <App reduce={reduce} initialState={initialState} />,
  document.getElementById("root")
);



